/* eslint-disable jsx-a11y/anchor-is-valid */
import clsx from 'clsx';
import React, { useState } from 'react';
import { KTIcon } from '../../../_metronic/helpers';
import { getLayout, LayoutSetup, useLayout } from '../../../_metronic/layout/core';

const CreateWorkflowPage = () => {
    
    return (<>
     <div id="kt_app_toolbar" className="app-toolbar py-3 py-lg-6">
      <div
        id="kt_app_toolbar_container"
        className="app-container d-flex flex-stack container-fluid"
        style={{paddingLeft:'3px',
      paddingRight:'3px'
     }}
      >
        <div
          id="kt_page_title"
          data-kt-swapper="true"
          data-kt-swapper-mode="prepend"
          data-kt-swapper-parent="{default: '#kt_content_container', 'lg': '#kt_toolbar_container'}"
          className="page-title d-flex flex-wrap me-3 flex-column justify-content-center"
        >
          <h1 className="page-heading d-flex text-gray-900 fw-bold fs-3 my-0 flex-column justify-content-center">
          Create Workflow
          </h1>
          <ul className="breadcrumb breadcrumb-separatorless fw-semibold fs-7 my-0 pt-1">
  {/*begin::Item*/}
  <li className="breadcrumb-item text-muted">
    <a
      href="/metronic8/demo1/../demo1/index.html"
      className="text-muted text-hover-primary"
    >
      Home{" "}
    </a>
  </li>
  {/*end::Item*/}
  {/*begin::Item*/}
  <li className="breadcrumb-item">
    <span className="bullet bg-gray-500 w-5px h-2px" />
  </li>
  {/*end::Item*/}
  {/*begin::Item*/}
  <li className="breadcrumb-item text-muted">Dashboards</li>
  {/*end::Item*/}
</ul>
        </div>
        <div className="d-flex align-items-center gap-2 gap-lg-3">
        <a href="#" className="btn btn-sm btn-flex btn-secondary fw-bold">
           View
          </a>
          <a href="#" className="btn btn-sm fw-bold btn-primary">
            Create
          </a>
        </div>
      </div>
    </div>
      <div className="card">
  {/*begin::Card header*/}
  <div className="card-header">
    {/*begin::Card title*/}
    <div className="card-title fs-3 fw-bold">Create Workflow</div>
    {/*end::Card title*/}
  </div>
  {/*end::Card header*/}
  {/*begin::Form*/}
  <form id="kt_project_settings_form" className="form">
    {/*begin::Card body*/}
    <div className="card-body p-9">
      {/*begin::Row*/}
      <div className="row mb-5">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Workflow Logo</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        <div className="col-lg-8">
          {/*begin::Image input*/}
          <div
            className="image-input image-input-outline"
            data-kt-image-input="true"
            style={{
              backgroundImage: 'url("assets/media/svg/avatars/blank.svg")'
            }}
          >
            {/*begin::Preview existing avatar*/}
            <div
              className="image-input-wrapper w-125px h-125px bgi-position-center"
              style={{
                backgroundSize: "75%",
                backgroundImage:
                  'url("assets/media/svg/brand-logos/volicity-9.svg")'
              }}
            />
            {/*end::Preview existing avatar*/}
            {/*begin::Label*/}
            <label
              className="btn btn-icon btn-circle btn-active-color-primary w-25px h-25px bg-white shadow"
              data-kt-image-input-action="change"
              data-bs-toggle="tooltip"
              title="Change avatar"
            >
              <i className="ki-duotone ki-pencil fs-7">
                <span className="path1" />
                <span className="path2" />
              </i>
              {/*begin::Inputs*/}
              <input type="file" name="avatar" accept=".png, .jpg, .jpeg" />
              <input type="hidden" name="avatar_remove" />
              {/*end::Inputs*/}
            </label>
            {/*end::Label*/}
            {/*begin::Cancel*/}
            <span
              className="btn btn-icon btn-circle btn-active-color-primary w-25px h-25px bg-white shadow"
              data-kt-image-input-action="cancel"
              data-bs-toggle="tooltip"
              title="Cancel avatar"
            >
              <i className="ki-duotone ki-cross fs-2">
                <span className="path1" />
                <span className="path2" />
              </i>
            </span>
            {/*end::Cancel*/}
            {/*begin::Remove*/}
            <span
              className="btn btn-icon btn-circle btn-active-color-primary w-25px h-25px bg-white shadow"
              data-kt-image-input-action="remove"
              data-bs-toggle="tooltip"
              title="Remove avatar"
            >
              <i className="ki-duotone ki-cross fs-2">
                <span className="path1" />
                <span className="path2" />
              </i>
            </span>
            {/*end::Remove*/}
          </div>
          {/*end::Image input*/}
          {/*begin::Hint*/}
          <div className="form-text">Allowed file types: png, jpg, jpeg.</div>
          {/*end::Hint*/}
        </div>
        {/*end::Col*/}
      </div>
      {/*end::Row*/}
      {/*begin::Row*/}
      <div className="row mb-8">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Workflow Name</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        <div className="col-xl-9 fv-row">
          <input
            type="text"
            className="form-control form-control-solid"
            name="name"
            defaultValue="9 Degree Award"
          />
        </div>
      </div>
      {/*end::Row*/}
      {/*begin::Row*/}
      <div className="row mb-8">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Workflow Type</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        <div className="col-xl-9 fv-row">
          <input
            type="text"
            className="form-control form-control-solid"
            name="type"
            defaultValue="Client Relationship"
          />
        </div>
      </div>
      {/*end::Row*/}
      {/*begin::Row*/}
      <div className="row mb-8">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Workflow Description</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        <div className="col-xl-9 fv-row">
          <textarea
            name="description"
            className="form-control form-control-solid h-100px"
            defaultValue={
              "Organize your thoughts with an outline. Here’s the outlining strategy I use. I promise it works like a charm. Not only will it make writing your blog post easier, it’ll help you make your message"
            }
          />
        </div>
        {/*begin::Col*/}
      </div>
      {/*end::Row*/}
      {/*begin::Row*/}
      <div className="row mb-8">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Due Date</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        <div className="col-xl-9 fv-row">
          <div className="position-relative d-flex align-items-center">
            <i className="ki-duotone ki-calendar-8 position-absolute ms-4 mb-1 fs-2">
              <span className="path1" />
              <span className="path2" />
              <span className="path3" />
              <span className="path4" />
              <span className="path5" />
              <span className="path6" />
            </i>
            <input
              className="form-control form-control-solid ps-12"
              name="date"
              placeholder="Pick a date"
              id="kt_datepicker_1"
            />
          </div>
        </div>
        {/*begin::Col*/}
      </div>
      {/*end::Row*/}
      {/*begin::Row*/}
      <div className="row mb-8">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Notifications</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        <div className="col-xl-9">
          <div className="d-flex fw-semibold h-100">
            <div className="form-check form-check-custom form-check-solid me-9">
              <input
                className="form-check-input"
                type="checkbox"
                defaultValue=""
                id="email"
              />
              <label className="form-check-label ms-3" htmlFor="email">
                Email
              </label>
            </div>
            <div className="form-check form-check-custom form-check-solid">
              <input
                className="form-check-input"
                type="checkbox"
                defaultValue=""
                id="phone"
                defaultChecked="checked"
              />
              <label className="form-check-label ms-3" htmlFor="phone">
                Phone
              </label>
            </div>
          </div>
        </div>
        {/*end::Col*/}
      </div>
      {/*end::Row*/}
      {/*begin::Row*/}
      <div className="row">
        {/*begin::Col*/}
        <div className="col-xl-3">
          <div className="fs-6 fw-semibold mt-2 mb-3">Status</div>
        </div>
        {/*end::Col*/}
        {/*begin::Col*/}
        <div className="col-xl-9">
          <div className="form-check form-switch form-check-custom form-check-solid">
            <input
              className="form-check-input"
              type="checkbox"
              defaultValue=""
              id="status"
              name="status"
              defaultChecked="checked"
            />
            <label
              className="form-check-label fw-semibold text-gray-400 ms-3"
              htmlFor="status"
            >
              Active
            </label>
          </div>
        </div>
        {/*end::Col*/}
      </div>
      {/*end::Row*/}
    </div>
    {/*end::Card body*/}
    {/*begin::Card footer*/}
    <div className="card-footer d-flex justify-content-end py-6 px-9">
      <button
        type="reset"
        className="btn btn-light btn-active-light-primary me-2"
      >
        Discard
      </button>
      <button
        type="submit"
        className="btn btn-primary"
        id="kt_project_settings_submit"
      >
        Create 
      </button>
    </div>
    {/*end::Card footer*/}
  </form>
  {/*end:Form*/}
</div>

    </>);
};
export { CreateWorkflowPage };
